body {
  margin: 0;
  height: 100vh;
}

.main-content {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.backimage {
  opacity: 0.5;
  width: 100%;
  height: 100vh;
  position: absolute;
  background-image: url("../images/bodyo.jpg");
  background-color: #aaa;
  background-size: cover;
  background-repeat: no-repeat;
}

.main-page {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-page .card {
  backdrop-filter: blur(1.5vh);
  width: 70%;
  height: 70vh;
  border: 1vh solid aqua;
  border-radius: 4.5vh;
}
.main-page .lvlbutton {
  width: 90%;
  height: 17vh;
  display: block;
  margin: 4vh auto;
  color: black;
  text-decoration: none;
  font-family: cursive;
  font-size: 12vh;
  text-align: center;
  border-radius: 4.5vh;
  border: 1vh solid aqua;
}